import React from 'react'
import { Link, navigate } from 'gatsby'
import { Location } from '@reach/router'
import { determineLanguage } from 'components/DisplayLanguageDeterminer'

export default function Nav({ onMenuToggle = () => {} }) {
  return (
    <nav id="nav">
      <ul>
        <li className="special">
          <a
            href="#menu"
            onClick={e => {
              e.preventDefault()
              onMenuToggle()
            }}
            className="menuToggle"
          >
            <span>Menu</span>
          </a>
          <div id="menu">
            <Location>
              {
                ({ location }) => {
                  const currentPath = location.pathname
                  const selectedValue = determineLanguage({ path: currentPath })

                  return (
                    <select
                      id='langSelect'
                      value={selectedValue}
                      onChange={
                        event => {
                          event.preventDefault()
                          navigate(document.getElementById('langSelect').value)
                        }
                      }>
                      <option key="en" value="/">English</option>
                      <option key="zh-Hans" value="/zh-Hans">简体中文</option>
                    </select>
                  )
                }
              }
            </Location>
            <br/>

            <ul>
              <li>
                <Link to="/" className="col-6">Home</Link>
              </li>
              <li>
                <Link to="/about" className="col-6">About Us</Link>
              </li>
              <li>
                <Link to="/gatherings" className="col-6">Gatherings</Link>
              </li>
              <li>
                <Link to="/sharings" className="col-6">Sharings</Link>
              </li>
              <li>
                <Link to="/giving" className="col-6">Giving</Link>
              </li>
              <li>
                <a href="https://www.flickr.com/photos/182245855@N04/albums" target="_blank" rel="noopener noreferrer" className="col-6">Gallery</a>
              </li>
              <li>
                <a href="https://www.youtube.com/channel/UCOff0cemmvakSbkV4HhNzbA/live" target="_blank" rel="noopener noreferrer" className="col-6">Live Stream</a>
              </li>
            </ul>
            <a
              className="close"
              onClick={
                e => {
                  e.preventDefault()
                  onMenuToggle()
                }
              }
              href="#menu"
            >
              {''}
            </a>
          </div>
        </li>
      </ul>
    </nav>
  )
}
